<template>
  <div class="panel panel-default">
    <div class="panel-heading clearfix">
      <div class="pull-left">
        <div class="checkbox">
          <label>
            <input :disabled="data1.length === 0" type="checkbox" @click="toggleAll()"
                   :checked="selectedAllStatus"><span>{{title}}</span>
          </label>
        </div>
      </div>
      <span class="pull-right">{{selectItemNumber}}/{{data1.length}}</span>
    </div>
    <div class="panel-body">
      <ul>
        <li v-for="item in data1" :key="item.field" v-dragging="{ item: item, list: data1, group: 'data1' }">
          <div class="checkbox">
            <label>
              <input type="checkbox" v-model="item.isSelected"> {{item.title}}
            </label>
          </div>
        </li>
      </ul>
    </div>
  </div>
</template>
<script>
  export default {
    name: 'ChangeBox',
    props: ['title', 'data1'],
    computed: {
      // 选择的数量
      selectItemNumber() {
        return this.data1.filter(item => item.isSelected).length;
      },
      // 全选状态
      selectedAllStatus() {
        if (
          this.selectItemNumber === this.data1.length &&
          this.selectItemNumber !== 0
        ) {
          return true;
        } else {
          return false;
        }
      }
    },
    methods: {
      // 全选及反选
      toggleAll() {
        let len = this.data1.length;
        let slen = this.data1.filter(item => item.isSelected).length;
        if (len !== slen) {
          this.data1.map(item => (item.isSelected = true));
        } else {
          this.data1.map(item => (item.isSelected = false));
        }
      }
    }
  };
</script>
<style scoped>
  ul {
    list-style: none;
    padding: 0;
  }

  .checkbox {
    margin: 0;
  }
  .panel{padding:15px;margin-bottom:20px;background-color:#fff;border:1px solid #ddd;border-radius:4px;-webkit-box-shadow:0 1px 1px rgba(0,0,0,0.05);box-shadow:0 1px 1px rgba(0,0,0,0.05)}.panel-heading{padding:10px 15px;margin:-15px -15px 15px;background-color:#f5f5f5;border-bottom:1px solid #ddd;border-top-right-radius:3px;border-top-left-radius:3px}.panel-title{margin-top:0;margin-bottom:0;font-size:17.5px;font-weight:500}.panel-footer{padding:10px 15px;margin:15px -15px -15px;background-color:#f5f5f5;border-top:1px solid #ddd;border-bottom-right-radius:3px;border-bottom-left-radius:3px}.panel-primary{border-color:#428bca}.panel-primary .panel-heading{color:#fff;background-color:#428bca;border-color:#428bca}.panel-success{border-color:#d6e9c6}.panel-success .panel-heading{color:#468847;background-color:#dff0d8;border-color:#d6e9c6}.panel-warning{border-color:#fbeed5}.panel-warning .panel-heading{color:#c09853;background-color:#fcf8e3;border-color:#fbeed5}.panel-danger{border-color:#eed3d7}.panel-danger .panel-heading{color:#b94a48;background-color:#f2dede;border-color:#eed3d7}.panel-info{border-color:#bce8f1}.panel-info .panel-heading{color:#3a87ad;background-color:#d9edf7;border-color:#bce8f1}
  .clearfix:before,.clearfix:after{display:table;content:" "}.clearfix:after{clear:both}.pull-right{float:right}
  .pull-left{float:left}
  .pull-right{float:right;padding-right:15px;padding-left:0;border-right:5px solid #eee;border-left:0}blockquote.pull-right p,blockquote.pull-right small{text-align:right}blockquote.pull-right small:before{content:''}blockquote.pull-right small:after{content:'\00A0 \2014'}q:before,q:after,blockquote:before,blockquote:after{content:""}address{display:block;margin-bottom:20px;font-style:normal;line-height:1.428571429}
</style>



