<template>
    <div class="container">
      <div class="row">
        <div class="col-md-5">
          <change-box-area :title="sourceTitle" :data="sourceList"></change-box-area>
        </div>
        <div class="col-md-2 text-center">
          <p style="margin-top: 20px">
            <button :disabled="sourceList.length === 0 || sourceRefNum === 0" class="btn btn-primary" @click="toTarget()">
              》
            </button>
          </p>
          <p style="margin-top: 10px">
            <button :disabled="targetList.length === 0 || targetRefNum === 0" class="btn btn-primary" @click="toSource()">
              《
            </button>
          </p>
        </div>
        <div class="col-md-5">
          <change-box-area_bak :title="targetTitle" :data1="targetList"></change-box-area_bak>
        </div>
      </div>
      <div class="ckq-modal-footer">
        <button @click="dialogClick" type="button" class="ckq-btn ckq-btn-primary ckq-btn-large">
          <span>确定</span>
        </button>
      </div>

  </div>

</template>
<script>
  import ChangeBoxArea from './ChangeBoxArea';
  import ChangeBoxArea_bak from './ChangeBoxArea_bak';
  // 这里的 isSeleted 属性可以不用添加，可以在 JS 中进行处理，一般情况下后端返回的数据也不会带有类似这种静态状态的属性
  export default {
    props: {
      dataList: {
        type: Array,
        default: null,
        require: false
      },
      sourdataList: {
        type: Array,
        default: null,
        require: false
      }
    },
    components: {
      ChangeBoxArea,
      ChangeBoxArea_bak
    },
    name: "ChangeBox",
    data() {
      return {
        sourceTitle: '请选择',
        targetTitle: '全选',
        sourceList: this.dataList,
        targetList: this.sourdataList
      };
    },
    methods: {
      dialogClick() {
        this.$emit('child-say', {sourceList:this.sourceList,targetList:this.targetList});
      },
      exchange(fd, td) {
        let selectedItem = fd.filter(item => item.isSelected).map(item => {
          return {
            ...item,
            isSelected: false
          };
        });
        td.push(...selectedItem);
        return fd.filter(item => !item.isSelected);
      },
      // 把选择数据转移到目标（右框）
      toTarget() {
        this.sourceList = this.exchange(this.sourceList, this.targetList);
      },
      // 把选择数据转回到源（左框）
      toSource() {
        this.targetList = this.exchange(this.targetList, this.sourceList);
      }
    },
    computed: {
      // 源数据中选中的数量
      sourceRefNum() {
        return this.sourceList.filter(item => item.isSelected).length;
      },
      // 目标数据中选中的数量
      targetRefNum() {
        return this.targetList.filter(item => item.isSelected).length;
      }
    },
    created() {
      console.log('000')
      console.log(this.sourceList)
      console.log(this.targetList)
    }
  };
</script>

